body {
  background: #000;
}
.scene{
    display: block;
    position: relative;
    width: 530px;
    height: 780px;
    margin: 20px auto;
    background: #4d2403;
    border-radius: 5px;
    overflow: hidden;
}
.scene:after {
  content: "";
  position: absolute;
  box-sizing: border-box;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border:30px solid #4d2403;
}
.scene .element{
    display: block;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 500px;
    height: 756px;
    margin: auto;
}
.scene .element:first-child {
  background-image: url(../img/Mona_Lisa3.png);
  background-size: contain;
  background-repeat: no-repeat;
}
.scene .element:nth-child(2){
  background-image: url(../img/Mona_Lisa2.png);
  background-size: contain;
  background-repeat: no-repeat;
}
.scene .element:last-child{
  background-image: url(../img/Mona_Lisa1.png);
  background-size: contain;
  background-repeat: no-repeat;
}
